* {
  box-sizing: content-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Outfit, sans-serif;
}

body {
  height: 100%;
  padding-bottom: 250px;
  position: relative;
}

ol, ul {
  list-style: none;
}

header {
  height: 50px;
  margin-bottom: 50px;
  padding: 20px 20px 0;
}

footer {
  width: 100%;
  height: 240px;
  background-color: #f3f3f3;
  position: absolute;
  bottom: 0;
}

a {
  text-decoration: none;
}

button {
  background-color: #0000;
  border-style: none;
}

.flex {
  display: flex;
}

.nav {
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.nav__left {
  width: 50%;
  justify-content: space-between;
  align-items: center;
}

.nav__left button {
  display: none;
}

.nav__left a {
  color: #d183c9;
}

.nav__right {
  width: 10%;
  justify-content: space-between;
  align-items: center;
}

.menu {
  width: 75%;
  justify-content: space-between;
  align-items: center;
}

.menu li {
  font-size: 100%;
}

.form {
  padding: 3% 1%;
}

.form__format li {
  align-items: center;
  margin-bottom: 3%;
  display: flex;
}

.form__format h5 {
  color: #643a71;
  height: 26px;
  align-items: center;
  margin-right: 5%;
  display: flex;
}

.form__format input {
  width: fit-content;
  border-radius: 8px;
  padding: 1%;
  font-family: Outfit, sans-serif;
}

.form__format button {
  width: 25%;
  background-color: #f3f3f3;
  border-style: groove;
  border-radius: 8px;
  padding: 10px;
}

.form__format #product_img {
  background-color: #0000;
}

.footer__container {
  width: 100%;
  height: 50%;
  justify-content: space-around;
  align-items: flex-start;
  padding-top: 1%;
  padding-bottom: 1%;
}

.footer__container a {
  color: #d183c9;
  padding-left: 1%;
  padding-right: 1%;
  font-size: 100%;
  font-weight: 300;
}

.footer__subcontainer {
  text-align: center;
  width: 10%;
  flex-direction: column;
}

.footer__subtitle {
  color: #643a71;
  font-size: 100%;
}

.footer__list {
  flex-direction: column;
  margin: 1%;
}

.footer__list li {
  width: 100%;
  padding-top: 1%;
}

.image__link {
  align-items: center;
  margin-top: 5%;
}

.image__link img {
  padding-right: 1%;
}

#divider {
  border-color: #d183c97a;
}

